.container {
  /*background-color: red;*/
  width: 100%;
    min-height: 100vh;
    background-color: slategrey;
    position: absolute;
    z-index: -1;
    
}

.container-content{
  top: 0;
  position : absolute;
  padding-bottom: 100vh;
}

.white-background {
  background-color: white;
  margin: 3rem 3rem 3rem 3rem;
  border: solid;
  border-color: lightgray;
  border-radius: 0rem 1rem 1rem 0rem;
  width: 70vw;
  height: 83vh;
  position: fixed;
  overflow: hidden;
  left: 13%;
  z-index: -1;
}

.app-table {
}

.nav-bar {
  position: fixed;
  overflow: hidden;
  background-color: #fff;
  left: 20px;
  width: 15%;
  height: 83vh;
  /*top: 7%;*/
  margin-top: 3rem;
  margin-bottom: 3rem;
  border: solid;
  border-color: lightgray;
  border-radius: 1rem 0rem 0rem 1rem;
  
}

.page-cont {
  
}

.scrollable-content {
  position: relative;
  
}

.body-header {
  top: 0;
  width: 100%;
  background-color: slategrey;
  height: 7vh;/*15%*/
  position: fixed;
  z-index: 1;
}

.body-footer {
  bottom: 0;
  width: 100%;/*70% or 800px*/
  background-color: slategrey;
  height: 9vh;/*9% 63px*/
  position: fixed;
  z-index: 1;
}

.body-right-mask {
  left : 87%;
  top: 0;
  width: 13%;/*70% or 800px*/
  background-color: slategrey;
  height: 100vh;/*9% 63px*/
  position: fixed;
  z-index: 1;
}

