
:root{
    --app-custom-color1: linear-gradient(to right, #68B0EC, #F6F4F7,#CBAEE8, #984D7E,#AE57C8);
}

.debit-card {
    
    border-color: lightgray;
    border-radius: 0.5rem;
    padding: 0.5rem 0.5rem;
    width: 160px;
    height: 100px;
    
    box-shadow: -10px 0 6px -6px rgb(0 0 0 / 0.2);
    
}

.colored {
    background-color: #68B0EC; /*lightcyan*/
    transform: rotate(40deg);
}

.white {
    background-color:#F6F4F7; /*snow*/
    transform: rotate(70deg);
    /*animation: roll 3s infinite;*/
}

@keyframes roll {
    0% {
      transform: rotate(0) translate(0, 0);
      /*transform: translate(0, 0);    */
    }
    100% {
      transform: rotate(60deg) translate(1000px, 100px);/*translate(w, h)*/
      /*transform: translate(20px, 50px); */   

    }
  }



.circle {
    position: relative;
    width: 13px;
    height: 13px;
    margin: 10px;
    /*transform: rotate(45deg);*/
    margin-left: 5px;
    margin-top: 0px;
    
}
.child-circle1 {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0.7;
    background: red;
    border-radius: 50%;
}
.child-circle2 {
    z-index: 1;
    /*margin: 5px;*/
    margin-left: 8px;
    background: darkorange;
}

