.phone-background {
    background-color: white;
    border: solid 8px;
    border-color: lightgray;
    border-radius: 1rem;
    padding: 0.5rem 0.5rem;
    width: 200px;
    height: 400px;
    border-style: groove;
    /*box-shadow: 0 -10px 6px -20px #AE57C8;*/
    box-shadow: -1px -2.8px 2.2px rgba(128, 0, 128, 0.034),
    -3px -6.7px 5.3px rgba(128, 0, 128, 0.048),
    -5px -12.5px 10px rgba(128, 0, 128, 0.06),
    -7px -22.3px 17.9px rgba(128, 0, 128, 0.072),
    -8px -41.8px 33.4px rgba(128, 0, 128, 0.086),
    -10px -100px 80px rgba(128, 0, 128, 0.1);

    
    
}



.table-data {
    border-collapse:separate;
    border-spacing:0 15px;
    text-align:center;
    padding: 8px 0px 8px 0px;
    font-size: 0.7rem;
}

.td-right {
    text-align:right;
    padding: 1px 0px 1px 20px;
}

.td-left {
    text-align:left;
    /*padding: 5px;*/
    padding: 1px 1px 1px 0px;
   
}

.td-center {
    text-align:center;
    /*padding: 5px;*/
    padding: 1px 0px 1px 20px;
   
}

.phone-amount {
    font-weight: bold;
    font-size: 1.5rem;
    transform: rotate(-2deg);
    
}

.phone-amount span {
    background-color: lightgrey;
    padding: 5px 5px;
    
    border-radius: 10px;
    outline-style: double;
    outline-width: 5px;
    outline-color: lightgrey;
    
}

.phone-th {
    padding-bottom: 30px;
}

