
:root {
    --clr-red-light: hsl(360, 71%, 66%);
    --clr-grey-6: hsl(209, 23%, 60%);

    --clr-white: #fff;
    --clr-black: #222;
    --spacing: 0.2rem;
    
    /*--app-custom-color1: linear-gradient(to right, #68B0EC, #F6F4F7,#CBAEE8, #984D7E,#AE57C8);*/


}

.right {
    align-items: right;
    margin-left: 330px;/*330px;*/
}

.body-content{
    align-items: left;
    margin-left: 2rem;
    padding-left: 2rem;
    text-align: left;
    position: relative;
}






.welcome-back {

     padding: 1rem 1rem;
     
}



  .lets-talk-btn {
    background: var(--clr-white);
    color: var(--clr-black);
    padding: 0.5rem 1rem;
    margin-left: 15rem;
    display: inline-block;
    font-weight: 700;
    transition: var(--transition);
    font-size: 0.65rem;
    border: solid 2px;
    cursor: pointer;
    box-shadow: 0 1px 3px #00000033;
    border-radius: 1rem;
    border-color: darkmagenta;
    
  }

  .lets-talk-btn:hover {
    color: var(--clr-white);
    background: var(--clr-black);
  }


  .support-btn {
    background: darkmagenta/*darkmagenta*/;
    color: var(--clr-white);
    padding: 0.5rem 1rem;
    margin-left: 15rem;
    margin-top: 27rem;
    display: inline-block;
    font-weight: 700;
    transition: var(--transition);
    font-size: 0.65rem;
    border: solid 2px;
    cursor: pointer;
    box-shadow: 0 1px 3px #00000033;
    border-radius: 1rem;
    border-color: darkmagenta;
    margin-top: 0px;
    margin-bottom: 0px;
    
  }

  .support-btn:hover {
    color: var(--clr-black);
    background: var(--clr-white);
  }

  
  .bottom-row {
      position: absolute;
      padding-top: 450px;
      padding-bottom: 0px;
      padding-left: 180px;
  }